@Component({
  components: {
    ErrorMessage,
    IconButton,
    Icon,
    IoTsError,
    ForbiddenError,
    LoggedOutError,
    Message,
    AjaxCodeError,
    DecoderError,
    NetworkError,
    InternalServerError
