













import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { GradientColor } from "@/types/colors";
import Gradient from "@/components/Reusable/CustomInputs/3rdPartyWrappers/Gradient/Gradient.vue";

const DEFAULT_GRADIENT = {
  type: "linear",
  degree: 0,
  points: [
    {
      left: 0,
      red: 0,
      green: 0,
      blue: 0,
      alpha: 1
    },
    {
      left: 100,
      red: 255,
      green: 0,
      blue: 0,
      alpha: 1
    }
  ]
};

@Component({
  components: { Gradient }
})
export default class GradientPicker extends Vue {
  @Prop({
    default: () => DEFAULT_GRADIENT
  })
  value!: GradientColor;

  @Prop({ default: "linear" })
  type!: "linear" | "radial";

  onGradientSelected(gradient: GradientColor) {
    this.$emit("input", gradient);
  }
}
