















































































































import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { Icon, IconSet, IconSetsV } from "@/types/icons";
import { configStore } from "@/store/store";
import defensiveCopy from "@/util/defensive-copy";
import IconUpload from "./IconUpload.vue";
import * as data from "@/lib/wordpress/data";
import { pipe } from "fp-ts/lib/function";
import { fold } from "fp-ts/lib/Either";
import NotificationLogger from "@/lib/notifications/notifications";
import TextInput from "@/components/Reusable/CustomInputs/TextInput.vue";
import SearchInput from "@/components/Reusable/CustomInputs/SearchInput.vue";
import IconComponent from "@/components/UI/Icon.vue";
import Dialog from "@/components/UI/Dialog.vue";

enum Mode {
  Upload = "upload",
  Browse = "browse"
}

@Component({
  components: { Dialog, IconComponent, SearchInput, TextInput, IconUpload }
})
export default class IconChooserDialog extends Vue {
  @Prop()
  open!: boolean;

  @Prop({
    default() {
      return [];
    }
  })
  suggestions!: string[];

  mode: Mode = Mode.Browse;
  searchString = "";

  activeIconSetIndex = 0;
  deleteInProgress = false;

  /* We change the key whenever upload mode is enabled
  to get vue to reset the IconUpload so that when the user
  leaves, if they come back the IconUpload is  cleared */
  uploadKey = "69";

  get placeholder() {
    return `Search ${this.availableIconsUnfiltered.length} Icons...`;
  }

  get availableIconsUnfiltered() {
    const icons = this.allIconSets[this.activeIconSetIndex].icons;

    return icons.map(icon => {
      return {
        ...icon,
        name: this.formatName(icon)
      };
    });
  }

  get availableIcons(): Icon[] {
    return this.availableIconsUnfiltered.filter(
      icon =>
        !this.searchString ||
        icon.name.includes(this.searchString) ||
        icon.slug.includes(this.searchString)
    );
  }

  get suggestedIcons() {
    const suggestions = this.suggestions.map(s => s.replace(/\s/g, "-"));

    return this.allIcons.filter(icon => {
      // Does the icon slug contains any substrings held in the suggestions array?
      return suggestions.some(v => icon.slug.includes(v));
    });
  }

  get allIcons() {
    return this.storedIconSets.flatMap(set => set.icons);
  }

  get extraIconSets() {
    const extra = [{ name: "All Icons", icons: this.allIcons, internal: true }];

    if (this.suggestions.length) {
      extra.unshift({
        name: "Suggested Icons",
        icons: this.suggestedIcons,
        internal: true
      });
    }

    return extra;
  }

  get storedIconSets() {
    return configStore.iconSets;
  }

  get allIconSets() {
    return [...this.extraIconSets, ...this.storedIconSets];
  }

  mounted() {
    this.retrieveIcons();
  }

  scrollToTop() {
    const container = this.$refs.scrollable as HTMLElement;
    container.scrollTop = 0;
  }

  async retrieveIcons() {
    if (configStore.iconSets.length) return;

    pipe(
      await data.retrieveFromWP("icons", IconSetsV),
      fold(
        errors =>
          errors.forEach(e =>
            NotificationLogger.log.message("icon chooser dialog error " + e.msg)
          ),
        iconSets => {
          configStore.setIconSets(iconSets);
        }
      )
    );
  }

  setActiveIconSetIndex(index: number) {
    this.activeIconSetIndex = index;
    this.mode = Mode.Browse;
    this.scrollToTop();
  }

  setUploadMode() {
    this.activeIconSetIndex = -1;
    this.mode = Mode.Upload;
    this.uploadKey = (Math.random() * 100).toString();
  }

  uploadComplete(modifiedIconSetIndex: number) {
    this.mode = Mode.Browse;
    this.activeIconSetIndex = modifiedIconSetIndex + this.extraIconSets.length;
  }

  async deleteIconSet(index: number) {
    if (index < this.extraIconSets.length) return;

    const iconSetIndex = index - this.extraIconSets.length;

    const confirmed = confirm(
      `Are you sure you want to delete ${this.storedIconSets[iconSetIndex].slug}?`
    );

    if (!confirmed) return;

    const newIconSets = defensiveCopy(this.storedIconSets);
    newIconSets.splice(iconSetIndex, 1);

    configStore.setIconSets(newIconSets);

    this.activeIconSetIndex = 0;
    this.deleteInProgress = true;

    await this.updateIcons(newIconSets);

    this.deleteInProgress = false;
  }

  async updateIcons(newIcons: IconSet[]) {
    pipe(
      await data.saveToWP("icons", newIcons),
      fold(
        errors => errors.forEach(e => NotificationLogger.log.message(e.msg)),
        () => {
          configStore.setIconSets(newIcons);
        }
      )
    );
  }

  formatName(icon: Icon) {
    return icon.name.replace(/icon-|\./g, "").replace(/-/g, " ");
  }
}
