import * as t from "io-ts";

// Solid & Gradient

export const SolidColorV = t.type({
  value: t.string,
  type: t.literal("solid")
});

export const GradientPointV = t.type({
  red: t.number,
  blue: t.number,
  green: t.number,
  alpha: t.number,
  left: t.number
});

export const GradientColorV = t.type({
  degree: t.number,
  points: t.array(GradientPointV),
  value: t.string,
  svgValue: t.string,
  type: t.union([t.literal("linear"), t.literal("radial")])
});

export const ColorV = t.union([SolidColorV, GradientColorV]);

// TODO: Remove string once it's not used in the codebase anymore.
export const ColorInputV = t.union([ColorV, t.string, t.null]);

// Global Colors
export const GlobalColorV = t.type({
  cssVariableName: t.string,
  label: t.string,
  value: t.union([t.null, t.string])
});

export const GlobalColorsV = t.array(GlobalColorV);

export type GlobalColor = t.TypeOf<typeof GlobalColorV>;
export type Color = t.TypeOf<typeof ColorV>;
export type ColorInputValue = t.TypeOf<typeof ColorInputV>;
export type SolidColor = t.TypeOf<typeof SolidColorV>;
export type GradientPoint = t.TypeOf<typeof GradientPointV>;
export type GradientColor = t.TypeOf<typeof GradientColorV>;
