


















































import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { DEFAULT_UNIT_TYPES, UnitInputValue } from "@/types/unit";
import UnitInput from "./UnitInput/UnitInput.vue";

/* these types suck ass and are not solid at all */

type SpacingInputValue = {
  left?: UnitInputValue;
  right?: UnitInputValue;
  top?: UnitInputValue;
  bottom?: UnitInputValue;
};

const SPACING_DIRECTIONS = ["left", "right", "top", "bottom"] as const;
/* Use the string values from array above to define a type */
type SpacingInputKey = typeof SPACING_DIRECTIONS[number];

@Component({
  components: { UnitInput }
})
export default class SpacingComplex extends Vue {
  @Prop({
    default: null
  })
  value!: SpacingInputValue | null;

  applyToAll = false;

  lastTouchedSpacingInput: SpacingInputKey = "top";

  get unitOptions() {
    return { types: DEFAULT_UNIT_TYPES };
  }

  edit(unitInputValue: UnitInputValue, key: SpacingInputKey) {
    this.lastTouchedSpacingInput = key;

    if (this.applyToAll) {
      this.applyUnitInputValueToAllAndEmit(unitInputValue);
    } else {
      const newValue: SpacingInputValue = {
        ...this.value,
        [key]: unitInputValue
      };

      this.$emit("input", newValue);
    }
  }

  toggleApplyToAll() {
    this.applyToAll = !this.applyToAll;

    if (this.applyToAll) {
      this.applyUnitInputValueToAllAndEmit(
        this.value?.[this.lastTouchedSpacingInput]
      );
    }
  }

  setLastTouchedSpacingInput(key: SpacingInputKey) {
    this.lastTouchedSpacingInput = key;
  }

  applyUnitInputValueToAllAndEmit(unitInputValue: UnitInputValue | undefined) {
    const newValue: SpacingInputValue = {};
    SPACING_DIRECTIONS.forEach(key => {
      newValue[key] = unitInputValue;
    });
    this.$emit("input", newValue);
  }
}
