













import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { ForbiddenErrorNotification } from "@/types/notifications";

@Component
export default class ForbiddenError extends Vue {
  @Prop()
  value!: ForbiddenErrorNotification;

  get errorMessage(): string {
    return (
      this.value.errorMessage ?? "Not enough permissions to perform the action."
    );
  }
}
