






































import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";

import { GradientPoint } from "@/types/colors";
import { EasyEvent } from "@/util/easy-event";

import {
  getRightValue,
  rgbToHsv,
  generateGradientStyle,
  generateSvgStyle
} from "@/components/Reusable/CustomInputs/3rdPartyWrappers/Gradient/helpers";
import Preview from "@/components/Reusable/CustomInputs/3rdPartyWrappers/Gradient/Preview/Preview.vue";
import GradientControls from "@/components/Reusable/CustomInputs/3rdPartyWrappers/Gradient/GradientControls.vue";
import Area from "@/components/Reusable/CustomInputs/3rdPartyWrappers/Gradient/Area/Area.vue";

type ActionName = "onStartChange" | "onChange" | "onEndChange";

@Component({
  components: {
    Area,
    GradientControls,
    Preview
  }
})
export default class Gradient extends Vue {
  @Prop({ default: "linear" })
  type!: string;

  @Prop({ default: 0 })
  degree!: number;

  @Prop({
    default: () => {
      return [
        {
          left: 0,
          red: 0,
          green: 0,
          blue: 0,
          alpha: 1
        },
        {
          left: 100,
          red: 255,
          green: 0,
          blue: 0,
          alpha: 1
        }
      ];
    }
  })
  points!: GradientPoint[];

  @Prop()
  onStartChange!: Function;

  @Prop()
  onChange!: Function;

  @Prop()
  onEndChange!: Function;

  activePointIndex = 0;
  gradientPoints = this.points;
  activePoint = this.points[0];
  colorRed = this.points[0].red;
  colorGreen = this.points[0].green;
  colorBlue = this.points[0].blue;
  colorAlpha = this.points[0].alpha;
  colorHue = 0;
  colorSaturation = 100;
  colorValue = 100;
  gradientType = this.type;
  gradientDegree = this.degree;
  isShiftActive = false;

  actions = {
    onStartChange: this.onStartChange,
    onChange: this.onChange,
    onEndChange: this.onEndChange
  };

  easyEvent = new EasyEvent();

  @Watch("type")
  onTypeChange() {
    this.changeGradientControl({ type: this.type });
  }

  mounted() {
    const { hue, saturation, value } = rgbToHsv({
      red: this.colorRed,
      green: this.colorGreen,
      blue: this.colorBlue
    });

    this.colorHue = hue;
    this.colorSaturation = saturation;
    this.colorValue = value;

    this.changeGradientControl({ type: this.type });

    this.easyEvent.on("keyup", this.keyUpHandler);
    this.easyEvent.on("keydown", this.keyDownHandler);
  }

  beforeDestroy() {
    this.easyEvent.removeAll();
  }

  removePoint(index = this.activePointIndex) {
    if (this.gradientPoints.length <= 2) {
      return;
    }

    this.gradientPoints.splice(index, 1);

    if (index > 0) {
      this.activePointIndex = index - 1;
    }

    this.onChange &&
      this.onChange({
        points: this.gradientPoints,
        type: this.gradientType,
        degree: this.gradientDegree,
        svgValue: generateSvgStyle(
          this.gradientPoints,
          this.gradientType,
          this.gradientDegree
        ),
        value: generateGradientStyle(
          this.gradientPoints,
          this.gradientType,
          this.gradientDegree
        )
      });
  }

  keyUpHandler(event: KeyboardEvent) {
    this.isShiftActive = event.shiftKey;

    // Delete / Backspace Key
    if (event.keyCode === 46 || event.keyCode === 8) {
      this.removePoint(this.activePointIndex);
    }
  }

  keyDownHandler(event: KeyboardEvent) {
    this.isShiftActive = event.shiftKey;
  }

  changeActivePointIndex(index: number) {
    this.activePointIndex = index;

    this.activePoint = this.gradientPoints[index];

    const { red, green, blue, alpha } = this.activePoint;

    this.colorRed = red;
    this.colorGreen = green;
    this.colorBlue = blue;
    this.colorAlpha = alpha;

    const { hue, saturation, value } = rgbToHsv({ red, green, blue });

    this.colorHue = hue;
    this.colorSaturation = saturation;
    this.colorValue = value;
  }

  changeGradientControl({ type, degree }: { type: string; degree?: number }) {
    const newType = getRightValue(type, this.gradientType);
    const newDegree = getRightValue(degree, this.gradientDegree);

    this.gradientType = newType;
    this.gradientDegree = newDegree;

    this.onChange({
      points: this.gradientPoints,
      type: this.gradientType,
      degree: this.gradientDegree,
      svgValue: generateSvgStyle(
        this.gradientPoints,
        this.gradientType,
        this.gradientDegree
      ),
      value: generateGradientStyle(
        this.gradientPoints,
        this.gradientType,
        this.gradientDegree
      )
    });
  }

  updateColor(
    {
      red,
      green,
      blue,
      alpha,
      hue,
      saturation,
      value
    }: {
      red: number;
      green: number;
      blue: number;
      alpha: number;
      hue: number;
      saturation: number;
      value: number;
    },
    actionName: ActionName = "onChange"
  ) {
    red = getRightValue(red, this.colorRed);
    green = getRightValue(green, this.colorGreen);
    blue = getRightValue(blue, this.colorBlue);
    alpha = getRightValue(alpha, this.colorAlpha);
    hue = getRightValue(hue, this.colorHue);
    saturation = getRightValue(saturation, this.colorSaturation);
    value = getRightValue(value, this.colorValue);

    const localGradientPoints = this.gradientPoints.slice();

    localGradientPoints[this.activePointIndex] = {
      ...localGradientPoints[this.activePointIndex],
      red,
      green,
      blue,
      alpha
    };

    this.colorRed = red;
    this.colorGreen = green;
    this.colorBlue = blue;
    this.colorAlpha = alpha;
    this.colorHue = hue;
    this.colorSaturation = saturation;
    this.colorValue = value;
    this.gradientPoints = localGradientPoints;

    const action = this.actions[actionName];

    action &&
      action({
        points: localGradientPoints,
        type: this.gradientType,
        degree: this.gradientDegree,
        svgValue: generateSvgStyle(
          this.gradientPoints,
          this.gradientType,
          this.gradientDegree
        ),
        value: generateGradientStyle(
          localGradientPoints,
          this.gradientType,
          this.gradientDegree
        )
      });
  }

  updateGradientLeft(
    left: number,
    index: number,
    actionName: ActionName = "onChange"
  ) {
    this.gradientPoints[index].left = left;

    const action = this.actions[actionName];

    action &&
      action({
        points: this.gradientPoints,
        type: this.gradientType,
        degree: this.gradientDegree,
        svgValue: generateSvgStyle(
          this.gradientPoints,
          this.gradientType,
          this.gradientDegree
        ),
        value: generateGradientStyle(
          this.gradientPoints,
          this.gradientType,
          this.gradientDegree
        )
      });
  }

  addPoint(left: number) {
    this.gradientPoints.push({
      ...this.gradientPoints[this.activePointIndex],
      left
    });

    this.activePointIndex = this.gradientPoints.length - 1;

    this.onChange &&
      this.onChange({
        points: this.gradientPoints,
        type: this.gradientType,
        degree: this.gradientDegree,
        svgValue: generateSvgStyle(
          this.gradientPoints,
          this.gradientType,
          this.gradientDegree
        ),
        value: generateGradientStyle(
          this.gradientPoints,
          this.gradientType,
          this.gradientDegree
        )
      });
  }
}
