






































import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import _ from "lodash";
// @ts-ignore
import { VueGpickr, LinearGradient } from "vue-gpickr";
import Dropdown from "@/components/Reusable/Dropdown/Dropdown.vue";
import Icon from "@/components/UI/Icon.vue";

const gradientDefault = {
  angle: 215,
  stops: [
    ["#009fff", 0],
    ["#ec2f4b", 1]
  ]
};

type GradientAttrs = {
  angle: number;
  stops: [string, number][];
  type: string;
};

type Values = {
  style?: string;
} & GradientAttrs;

type LinearGradientObject = {
  toRaw: () => GradientAttrs;
  toString: () => string;
} & GradientAttrs;

@Component({
  components: { Icon, Dropdown, VueGpickr }
})
export default class VueGradientPicker extends Vue {
  @Prop()
  value!: Values | null;

  gradient: LinearGradientObject = new LinearGradient(
    this.value || gradientDefault
  );

  // Slow down the updates to give a smooth UX
  throttledChange = _.throttle(this.onChange, 100);
  onChange(attrs: GradientAttrs) {
    this.gradient = new LinearGradient(attrs);

    this.$emit("input", {
      ...this.gradient.toRaw(),
      style: this.gradient.toString()
    });
  }

  clear() {
    this.$emit("input", {});
  }
}
