import * as t from "io-ts";

export const DesignLibraryDataV = t.type({
  customDesignSets: t.array(t.string)
});

export type DesignLibraryData = t.TypeOf<typeof DesignLibraryDataV>;

// Design Sets
export const DesignSetPostV = t.type({
  id: t.number,
  name: t.string,
  url: t.string,
  tags: t.array(t.string)
});

export const DesignSetDataV = t.type({
  enabled: t.boolean,
  // needed for backward compatibility. There may be design sets on an older version on Breakdance
  // without "reliesOnGlobalSettings"
  reliesOnGlobalSettings: t.union([t.boolean, t.undefined]),
  name: t.string,
  posts: t.array(DesignSetPostV)
});

export type DesignSetPost = t.TypeOf<typeof DesignSetPostV>;
export type DesignSetData = t.TypeOf<typeof DesignSetDataV>;

// Screenshot

export const ScreenshotV = t.type({ url: t.string, id: t.number });

export type Screenshot = t.TypeOf<typeof ScreenshotV>;

export type DesignProvider = {
  name: string;
  url: string;
  type?: string;
  isLocal?: true;
};
