





































import { Component, Prop, Vue } from "vue-property-decorator";
import TextInput from "@/components/Reusable/CustomInputs/TextInput.vue";
import DropdownInput from "@/components/Reusable/CustomInputs/DropdownInput.vue";
import { DynamicField } from "@/types/dynamic-data";
import { DynamicFieldReturnType } from "@/types/dynamic-property-path";

@Component({
  components: { TextInput, DropdownInput }
})
export default class DynamicDataField extends Vue {
  @Prop()
  field!: DynamicField;

  @Prop()
  active!: boolean;

  @Prop()
  selected!: boolean;

  @Prop()
  accepts!: DynamicFieldReturnType;

  get normalizedReturnTypes() {
    return this.field.returnTypes
      .map(returnType => this.normalize(returnType))
      .join(", ");
  }

  get normalizedAccepts() {
    return this.normalize(this.accepts);
  }

  normalize(text: string) {
    return text.replace("_", " ");
  }
}
