




























import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { GlobalColor } from "@/types/colors";
import ColorSwatch from "@/components/Reusable/CustomInputs/Color/ColorSwatch.vue";
import SaveColorDialog from "@/components/Reusable/CustomInputs/Color/SaveColorDialog.vue";

@Component({
  components: { ColorSwatch, SaveColorDialog }
})
export default class GlobalColorsList extends Vue {
  @Prop({
    default() {
      return [];
    }
  })
  value!: GlobalColor[];

  @Prop()
  selectedGlobalColorCssVariableName!: string;

  @Prop({ default: true })
  canSave!: boolean;

  @Prop()
  colorToSave!: string;

  isColorSelected(color: GlobalColor) {
    return this.selectedGlobalColorCssVariableName === color.value;
  }

  get globalColors() {
    return this.value.filter(color => {
      if (typeof color.value !== "string") return true;
      return !color.value.includes("var(--");
    });
  }

  onColorSelected(color: GlobalColor) {
    this.$emit("input", color);
  }
}
