






















import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { GlobalColor } from "@/types/colors";
import { globalStore } from "@/store/store";
import { createVariableName } from "@/lib/colors/colors";

import VueColorWrapper from "@/components/Reusable/CustomInputs/3rdPartyWrappers/VueColorWrapper.vue";
import Icon from "@/components/UI/Icon.vue";
import Dialog from "@/components/UI/Dialog.vue";
import TextInput from "@/components/Reusable/CustomInputs/TextInput.vue";
import { saveGlobalSettingsToWordPress } from "@/lib/global-settings/global-settings";
import { isUiPanelOpen } from "@/lib/panels/panels";

@Component({
  components: { Icon, VueColorWrapper, Dialog, TextInput }
})
export default class SaveColorDialog extends Vue {
  @Prop()
  value!: string;

  openDialog: boolean | null = null;

  label: string | null = "";

  saveAsGlobal() {
    if (!this.label) return;

    const color: GlobalColor = {
      cssVariableName: createVariableName(
        this.label,
        globalStore.globalColors.length + 1
      ),
      label: this.label,
      value: this.value
    };

    // TODO move this out of component, just emit the value (make component stupid)
    globalStore.addGlobalColor(color);

    // Unless global settings panel is already open, persist this on the server
    if (!isUiPanelOpen("globalSettings")) {
      saveGlobalSettingsToWordPress();
    }

    this.$emit("input", color);
    this.openDialog = false;
  }

  closeDialog() {
    this.label = "";
    this.openDialog = false;
  }
}
