import { Permission } from "@/types/permission";
import { BuilderElementControls } from "@/types/elements";
import { Control } from "@/types/controls";

export function getPermission(permissions: Permission[], permission: string) {
  return permissions.find(p => p.slug === permission);
}

export function getClientModeOnlyControls(controls: Control[]): Control[] {
  const allowedControls = ["background", "image"];

  return controls
    .filter(control => {
      return allowedControls.includes(control.slug);
    })
    .map(control => {
      return {
        ...control,
        children: getClientModeOnlyControls(control.children)
      };
    });
}

export function filterControlsThatCanBeModified(
  controls: BuilderElementControls,
  permission: Permission | null
) {
  if (permission?.slug === "full") {
    return controls;
  }

  const designSections = getClientModeOnlyControls(controls.designSections);

  return {
    ...controls,
    designSections,
    // Messing with Settings is not allowed in edit content access.
    settingsSections: []
  };
}
