import {
  ChangeControlValueMeta,
  Control,
  ControlWithinTree,
  ValueEmittedFromControlInputDecoratedWithMetadata
} from "@/types/controls";
import {
  DocumentStorePropertyChange,
  GenericStorePropertyChange,
  SelectorStorePropertyChange,
  SettingStorePropertyChange,
  StorePropertyChangeMetadata
} from "@/types/store-property-change";
import { uniq } from "lodash";
import {
  generateGenericStorePropertyChangeSnapshotLabelBasingOnChangedControl,
  getPropertyChangeSnapshotLabelAction
} from "@/lib/undo/snapshot-labels/generic-store-property-change.utils";
import { getBuilderElementNameByTreeNodeId } from "@/lib/undo/snapshot-labels/document-store-property-change.utils";
import { SnapshotLabelActionAndSubject } from "@/types/snapshot";

export function addHoverToPath(path: string): string {
  return `${path}_hover`;
}

export function findControlAncestors(
  controlWithinTree: ControlWithinTree
): Control[] | undefined {
  for (const controlsTreeNode of controlWithinTree.controlsTree) {
    const stack = [controlsTreeNode];
    if (controlsTreeNode.children.includes(controlWithinTree.control)) {
      return stack;
    } else {
      const ancestors = findControlAncestors({
        control: controlWithinTree.control,
        controlsTree: controlsTreeNode.children
      });

      if (ancestors) {
        return [...stack, ...ancestors];
      }
    }
  }

  return undefined;
}

export function addMetadataToDocumentPropertyChangeBasingOnControlValueChangeMetadata(
  propertyChangeWithoutMetadata: Omit<DocumentStorePropertyChange, "meta">,
  changedControlWithinTree: ControlWithinTree,
  controlValueChangeMetadata?: ChangeControlValueMeta
): DocumentStorePropertyChange {
  return addMetadataToGenericPropertyChangeBasingOnControlValueChangeMetadata(
    getBuilderElementNameByTreeNodeId(propertyChangeWithoutMetadata.elementId),
    propertyChangeWithoutMetadata,
    changedControlWithinTree,
    controlValueChangeMetadata
  );
}

export function addMetadataToSelectorPropertyChangeBasingOnControlValueChangeMetadata(
  propertyChangeWithoutMetadata: Omit<SelectorStorePropertyChange, "meta">,
  changedControlWithinTree: ControlWithinTree,
  controlValueChangeMetadata?: ChangeControlValueMeta
): SelectorStorePropertyChange {
  return addMetadataToGenericPropertyChangeBasingOnControlValueChangeMetadata(
    "Selectors",
    propertyChangeWithoutMetadata,
    changedControlWithinTree,
    controlValueChangeMetadata
  );
}

export function addMetadataToSettingPropertyChangeBasingOnControlValueChangeMetadata(
  propertyChangeWithoutMetadata: Omit<SettingStorePropertyChange, "meta">,
  changedControlWithinTree: ControlWithinTree,
  controlValueChangeMetadata?: ChangeControlValueMeta
): SettingStorePropertyChange {
  return addMetadataToGenericPropertyChangeBasingOnControlValueChangeMetadata(
    "Global Settings",
    propertyChangeWithoutMetadata,
    changedControlWithinTree,
    controlValueChangeMetadata
  );
}

export function addMetadataToGenericPropertyChangeBasingOnControlValueChangeMetadata<
  T extends Omit<GenericStorePropertyChange, "meta">
>(
  snapshotLabelActionSubject: SnapshotLabelActionAndSubject["subject"],
  propertyChangeWithoutMetadata: T,
  changedControlWithinTree: ControlWithinTree,
  controlValueChangeMetadata?: ChangeControlValueMeta
): T & { meta: StorePropertyChangeMetadata } {
  const action = getPropertyChangeSnapshotLabelAction(
    propertyChangeWithoutMetadata
  );

  const transactionBundlingKeySuffixParts: string[] = [action];

  if (
    // TODO in theory there may be multiple levels of nested controls, only one is handled as of now
    controlValueChangeMetadata?.nestedControlThatWasChanged &&
    !controlValueChangeMetadata.nestedControlThatWasChanged.meta
      ?.disableTransactionBundling
  ) {
    transactionBundlingKeySuffixParts.push(
      controlValueChangeMetadata.nestedControlThatWasChanged.path
    );
  }

  const transactionBundlingOptionsWhenItsEnabled = transactionBundlingKeySuffixParts.length
    ? { bundlingKeySuffix: transactionBundlingKeySuffixParts.join(":") }
    : undefined;

  return {
    ...propertyChangeWithoutMetadata,
    meta: {
      snapshotLabel: generateGenericStorePropertyChangeSnapshotLabelBasingOnChangedControl(
        { action, subject: snapshotLabelActionSubject },
        changedControlWithinTree,
        controlValueChangeMetadata?.nestedControlThatWasChanged
      ),
      transactionBundling: controlValueChangeMetadata?.disableTransactionBundling
        ? { disable: true }
        : transactionBundlingOptionsWhenItsEnabled
    }
  };
}

export function getControlLabelWithAncestorsLabelsFirst(
  controlWithinTree: ControlWithinTree
): string[] {
  const controlAncestors = findControlAncestors(controlWithinTree) || [];

  return uniq(
    [...controlAncestors, controlWithinTree.control].map(
      control => control.label
    )
  );
}

export function unwrapControlValue<T>(
  value: ValueEmittedFromControlInputDecoratedWithMetadata<T> | unknown
) {
  if (value instanceof ValueEmittedFromControlInputDecoratedWithMetadata) {
    return value.value;
  } else {
    return value;
  }
}

export function getBlankControl(): Control {
  return {
    slug: "new_control",
    label: "New Control",
    keywords: [],
    options: {
      type: "text",
      layout: "vertical"
    },
    enableMediaQueries: false,
    enableHover: false,
    children: []
  };
}

export function getBlankControlSection(): Control {
  return {
    slug: "new_section",
    label: "New Section",
    keywords: [],
    options: {
      type: "section",
      sectionOptions: {
        type: "popout"
      }
    },
    enableMediaQueries: false,
    enableHover: false,
    children: []
  };
}
